.sidebar {
    width: 230px;
    margin-top: 20px;

    @include inline-blocks();

    font-size: 0;

    a {
        text-decoration: none;
    }

    &__item {
        &:hover {
            background-image: linear-gradient(to top, #eceff1 0%, #ffffff 100%);
        }
    }

    .item__link {
        display: block;

        color: $grey3;
        font-family: $Museo_sans-900;
        font-size: 13px;
        line-height: 49px;
        font-weight: 900;
        text-transform: uppercase;
        &:hover {
            color: $blueColor;
        }

        span {
            margin-left: 12px;
        }
    }
    .icons {
        margin-top: 10px;
    }
    .icons_atm {
        @include bg-svg($atm);
    }
    .icons_branch {
        @include bg-svg($branch);
    }
    .icons_cards {
        @include bg-svg($cards);
    }
    .icons_myprod {
        @include bg-svg($myprod);
    }
    .icons_template {
        @include bg-svg($template);
    }
    .icons_exchange {
        @include bg-svg($exchange);
    }
}

.sidebar__item .item__link_myprod:hover .icons_myprod {
    @include bg-svg($myprod-blue);
}
.sidebar__item .item__link_template:hover .icons_template {
    @include bg-svg($template-blue);
}
.sidebar__item .item__link_branch:hover .icons_branch {
    @include bg-svg($branch-blue);
}
.sidebar__item .item__link_atm:hover .icons_atm {
    @include bg-svg($atm-blue);
}
.sidebar__item .item__link_cards:hover .icons_cards {
    @include bg-svg($cards-blue);
}
.sidebar__item .item__link_exchange:hover .icons_exchange {
    @include bg-svg($exchange-blue);
}
