.content-sec {
    background-color: $grey2;

    padding-top: 20px;
}





/*  overview  */

.overview {
    width: 31.2%;

    margin-top: 18px;

    @include inline-blocks();

    background: $white;

    box-shadow: 0 14px 14px rgba(0, 0, 0, 0.13);

    &:not(:last-child) {
        margin-right: 27px;
    }

    &__title {
        display: block;

        text-align: center;

        background-color: $grey4;

        color: $black;
        font-family: $Museo_sans-300;
        font-size: 13px;
        line-height: 49px;
        font-weight: 300;
        text-transform: uppercase;
    }
    &__title_det {
        background: $transparent;

        color: $grey5;
        font-size: 11px;
        line-height: 18px;
        text-transform: capitalize;
    }

    &__wrap {
        text-align: center;
        color: $grey3;
        font-family: $Museo_sans-Medium;
        font-size: 21px;
        line-height: 53px;
        font-weight: 500;
    }
    &__wrap_det {
        color: $blueColor;
        font-size: 17px;
        line-height: 18px;
    }

    &____val {
        margin-right: 5px;

        text-transform: uppercase;
    }

    .overview-prod__list {
        margin-bottom: 19px;
    }
    .overview-prod__item {
        @include inline-blocks();
        width: 32%;
    }
}



/*  services  */

.services {
    width: 47.5%;

    margin-top: 27px;

    @include inline-blocks();
    font-size: 0;



    &:not(:last-child) {
        margin-right: 45px;
    }

    &__head-block {
        box-shadow: 0 10px 23px 5px rgba($black, 0.05);
        border-radius: 2px;
        background-color: $white;

        cursor: pointer;
    }
    &__name {
        width: 81%;

        padding-left: 13px;

        @include inline-blocks();

        font-size: 0;
        box-sizing: border-box;
        .icons_chevron-blue {
            @include bg-svg($chevron-d);
            margin-top: 10px;
        }
        span {
            color: $grey6;
            font-family: $Museo_sans-700;
            font-size: 17px;
            line-height: 44px;
            font-weight: bold;
        }
    }
}

.sub-services {

    margin-top: 2px;

    box-shadow: 0 10px 11px 3px rgba($black, 0.09);
    background-color: $white;

    &__item {
        position: relative;
        padding-top: 20px;
        //&::after {
        //    content: "";
        //
        //    @include bg-svg($settings);
        //    position: absolute;
        //    top: 31px;
        //    right: 18px;
        //}
    }
    &__input {
        padding-left: 40px;

        @include inline-blocks();
        color: $blueColor;
        font-family: $Museo_sans-Medium;
        font-size: 14px;
        line-height: 24px;
        font-weight: 500;
    }
    .icons_write {
        @include inline-blocks();
        @include bg-svg($pencil);
        font-size: 0;

        cursor: auto;
    }

    &__acts {
        position: relative;
        .acts__inner {
            margin-left: 40px;

            @include inline-blocks();
            font-size: 0;


            span {
                margin-left: 10px;
            }
        }
    }
    .acts__link {
        color: $grey8;
        font-family: $Museo_sans-300;
        font-size: 16px;
        line-height: 24px;
        font-weight: 300;
        text-decoration: none;
    }
    .overview__wrap_acts {
        margin-left: 40px;
        @include inline-blocks();
        line-height: 24px;
        .overview__currency {
            color: $grey8;
            font-family: $Museo_sans-300;
            font-size: 16px;
            line-height: 24px;
            font-weight: 300;
        }
    }
    .icons {
        margin-top: 3px;
    }
    .icons_arrow-r {
        @include bg-svg($arrow-r);
    }
    .icons_transfer {
        @include bg-svg($transfer);
    }
    .icons_settings {
        position: absolute;
        top: -20px;
        right: 18px;

        @include bg-svg($settings);
    }
}