.header {
    background-color: $blueColor;
    padding-top: 34px;
    padding-bottom: 22px;
}

.container {
    width: 92.5%;
    max-width: 1400px;
    margin: 0 auto;
}

.top__logo {
    max-width: 335px;

    @include inline-blocks();
    margin-right: 55px;

    font-size: 0;

    img {
        width: 176px;
        height: 34px;
    }
}

.top__container {
    @include inline-blocks();

    font-size: 0;
}

.top__container__list_text {
    @include inline-blocks();
    & li {
        @include inline-blocks();
        margin-right: 60px;
    }
}

.list-text_elem {
    @include inline-blocks();
    text-decoration: none;

    color: #ffffff;
    font-family: $Museo_sans-700;
    font-size: 14px;

    line-height: 36px;
    font-weight: 700;
    transform: scale(1);
}

.list-text_elem:hover {
    transform: scale(1.1);
    transition: all 0.2s ease-out;
}

.top__container__lang {
    @include inline-blocks();

    font-size: 0;
}

.lang__select {
    width: 53px;
    height: 40px;

    border-radius: 1px;
    border: 1px solid #f3f3f3;
    background-color: #1471d1;

    color: $white;
    font-family: $Museo_sans-Medium;
    font-size: 14px;
    font-weight: 500;
    text-transform: uppercase;

    box-sizing: border-box;

    cursor: pointer;

    option {
        cursor: pointer;
    }
}

.top__container__list_img {
    @include inline-blocks();
    margin-top: 10px;
    margin-left: 30px;

    font-size: 0;
    li {
        @include inline-blocks();


        &:not(:first-child) {
            margin-left: 29px;
        }
    }
}


/* HEADER-BOTTOM*/

.container_content {
    width: 1024px;

    @include inline-blocks();
}

.bottom__client {
    @include inline-blocks();

    margin-right: 55px;

    font-size: 0;
    .client__img {
        @include inline-blocks();
        img {
            width: 34px;
            height: 32px;
        }
    }
    .client__name {
        width: 125px;
        margin-left: 13px;

        @include inline-blocks();
        color: $black;
        font-size: 12px;
        line-height: 14px;
        font-weight: 500;
    }
}

.bottom__buy-sell {
    @include inline-blocks();
    font-size: 0;
}

.currency {
    width: 49%;

    background: $white;

    @include inline-blocks();

    box-shadow: 0 4px 4px rgba(46, 46, 46, 0.1);

    box-sizing: border-box;

    &:not(:last-child) {
        margin-right: 10px;
    }
}

.currency__bid {
    @include inline-blocks();
    margin-right: 35px;
    padding-left: 20px;

    color: $grey;
    font-family: $Museo_sans-300;
    font-size: 13px;
    line-height: 35px;
    font-weight: 300;
}

.currency__item {

    @include inline-blocks();
    margin-right: 45px;
    span {
        color: $grey;
        font-size: 13px;
        line-height: 35px;
        font-weight: 500;

        opacity: 0.53;
        &:not(:last-child) {
            margin-right: 5px;
        }
    }
}
.currency__list p {
    display: inline-block;
    vertical-align: top;
    padding-right: 150px;
    padding-bottom: 20px;
}

.currency__list {
    @include inline-blocks();
    font-size: 0;
}

.currency__list__elem {
    margin-right: 50px;
}